/*----------------------------------
--------- THEME DEFAULT CSS --------
----------------------------------*/
/* @import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,900"); */
@import url("https://fonts.googleapis.com/css2?family=Raleway&display=swap");
body {
  line-height: 24px;
  font-size: 14px;
  color: #666666;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
}

/*----------------------------
---------- RESET CSS ---------
----------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0px;
}
h1 {
  font-size: 48px;
}
h2 {
  font-size: 30px;
  color: #232323;
}
h4 {
  font-size: 18px;
  color: #232323;
}
h6 {
  font-size: 14px;
  color: #232323;
}
h5 {
  color: #232323;
}
a {
  transition: 0.6s;
}
a,
a:hover {
  color: #666666;
}
img {
  max-width: 100%;
  height: auto;
}
iframe {
  width: 100%;
  border: none;
}
a,
a:hover,
a:focus,
a:active,
button:focus {
  text-decoration: none;
  border: none;
  outline: 0;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
ol {
  margin-bottom: 0;
}
select:focus,
select:active {
  box-shadow: none;
  border: 0;
  outline: 0;
}
input:focus,
textarea:focus,
select:focus,
button:focus {
  outline: none;
}
button {
  cursor: pointer;
}
textarea {
  height: 120px;
}
input[type="text"],
input[type="submit"],
button[type="submit"],
input[type="email"],
input[type="url"],
input[type="tel"],
input[type="search"],
input[type="password"],
input[type="number"],
textarea {
  background: transparent;
  width: 100%;
  padding: 6px 10px;
  border: 1px solid #ccc;
  margin-bottom: 15px;
}
input[type="submit"],
button[type="submit"] {
  width: auto;
  color: #666;
  padding: 4px 15px;
}
textarea:focus {
  outline: none !important;
  box-shadow: none !important;
}
.text-yellow {
  color: yellow;
}
.text-green {
  color: #006600;
}
.sec-title {
  text-align: center;
  max-width: 400px;
  margin: 0 auto 40px;
}
.sec-title h2 {
  padding-bottom: 20px;
  margin-bottom: 20px;
  position: relative;
  top: -6px;
}
.sec-title h2 .sec-title-border {
  width: 130px;
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 2px solid #222;
  right: 0;
  height: 0;
  margin: auto;
}
.pricing-area .sec-title h2 .sec-title-border,
.download-area .sec-title h2 .sec-title-border {
  border-color: #fff;
}
.pricing-area .sec-title h2 .sec-title-border span::before,
.download-area .sec-title h2 .sec-title-border span::before {
  background-color: #fff;
}
.sec-title h2 .sec-title-border span {
  width: 9px;
  height: 9px;
  position: relative;
  display: inline-block;
  margin: 0 1px;
  bottom: 23.3px;
}
.sec-title h2 .sec-title-border span::before {
  background: #222;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  transform: rotate(45deg);
}
.mt-100 {
  margin-top: 100px;
}
.mt-30 {
  margin-top: 30px;
}
.ptb-100 {
  padding: 100px 0;
}
.ptb-90 {
  padding: 90px 0;
}
.owl-nav div {
  position: absolute;
  top: 50%;
  left: 20px;
  width: 35px;
  background: #fff;
  color: #000;
  height: 35px;
  line-height: 36px;
  border-radius: 100%;
  text-align: center;
  font-size: 29px;
  transition: 0.4s;
  margin-top: -60px;
}
.owl-nav div:hover {
  background: #000;
  color: #fff;
}
.owl-nav div.owl-next {
  right: 20px;
  left: auto;
}
.owl-dots {
  text-align: center;
  margin-top: 30px;
}
.owl-dot {
  width: 10px;
  height: 10px;
  border: 1px solid #0074da;
  border-radius: 10px;
  display: inline-block;
  margin: 0 3px;
}
.owl-dot.active {
  background: #0074da;
}
.display-table {
  display: table;
  height: 100%;
  width: 100%;
}
.display-tablecell {
  display: table-cell;
  vertical-align: middle;
}
.header.sticky .mainmenu li a.appao-btn,
.mainmenu li a.appao-btn,
.appao-btn {
  border: 1px solid yellow;
  color: #fff;
  border-radius: 15px;
  padding: 6px 25px;
  display: inline-block;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 500;
}
.header.sticky .mainmenu li a.appao-btn:hover,
.mainmenu li a.appao-btn:hover,
.appao-btn:hover {
  background: yellow;
  border: 1px solid #fff;
  color: green;
  box-shadow: 5px 5px 10px black;
}
.appao-btn2 {
  border: 1px solid transparent;
  border-radius: 25px;
  background: #001a00; /* Old browsers */
  background-image: -moz-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* FF3.6-15 */
  background-image: -webkit-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background-image: linear-gradient(
    to right,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  );
}
.appao-btn2:hover {
  border: 1px solid#001a00;
  background: transparent;
  background-image: none;
  color: #001a00;
}
.flexbox-center {
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
input::-webkit-input-placeholder {
  color: #666;
}

.shadow {
  box-shadow: 0 0 5px #ccc;
}
.bg-white {
  background-color: #fff;
}

/*--------------------------------------
-------- HEADER AREA CSS START --------
---------------------------------------*/
.header {
  position: fixed;
  top: 0;
  left: 0;
  transition: all 0.4s;
  width: 100%;
  z-index: 99;
  padding: 18px 0;
}
.pointer {
  cursor: pointer;
}
/* .logo a {
  color: #ffffff;
  font-size: larger;
  font-style: normal;
  font-weight: 600;
  letter-spacing: 4px;
  border-top: 3px solid yellow;
  border-bottom: 3px solid yellow;
  padding: 5px;
} */
.move a {
  position: relative;
  animation-name: gentle-move;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}
@keyframes gentle-move {
  0% {
    left: -10px;
  }
  50% {
    border-color: #006600;
    left: 10px;
  }
  100% {
    left: -10px;
  }
}
.unseen {
  opacity: 0;
  border: 1px solid #fff;
  color: #fff;
  border-radius: 15px;
  padding: 6px 25px;
  display: inline-block;
  text-transform: uppercase;
  font-weight: 500;
  cursor: default;
}
.mainmenu {
  text-align: right;
}
.mainmenu li {
  display: inline-block;
  text-transform: uppercase;
}
.mainmenu li a {
  display: inline-block;
  color: #fff;
  position: relative;
  padding: 5px 16px;
  margin: 0 3px;
  border: 1px solid transparent;
}
.slicknav_menu {
  padding: 0;
  background: transparent;
}
.slicknav_menu a.active,
.mainmenu ul li a:hover,
.mainmenu ul li a.active {
  border: 1px solid yellow;
}
.mainmenu {
  display: block;
}
.slicknav_menu {
  display: none;
}
.header.sticky {
  border-bottom: 1px solid #fff;
  background: #001a00; /* Old browsers */
  background: -moz-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  );
}
.header.sticky {
  padding: 10px 0;
}
.mainmenu li a.appao-btn {
  margin-left: 55px;
}
/*--------------------------------------
--------- HEADER AREA CSS END ---------
---------------------------------------*/

/*--------------------------------------
-------- HERO AREA CSS START --------
---------------------------------------*/
.hero-area {
  padding-top: 140px;
  z-index: 1;
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  overflow: hidden;
}
.hero-area::before {
  content: "";
  top: 0;
  left: 0;
  opacity: 0.85;
  right: 0;
  bottom: 0;
  z-index: -1;
  position: absolute;
  background: #001a00; /* Old browsers */
  background: -moz-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  );
}
.hero-area-content {
  color: #fff;
  padding: 80px 0 170px;
}
.hero-area-content h1 {
  color: #fff;
}
.hero-area-content p {
  margin: 28px 0 25px;
}
.hero-area-content .appao-btn {
  margin-top: 15px;
  margin-right: 15px;
}
.hand-mockup {
  position: absolute;
  bottom: -2px;
  max-width: 100%;
}
/*------------------------------------
--------- HERO AREA CSS END ---------
------------------------------------*/

/*--------------------------------------
-------- ABOUT AREA CSS START --------
---------------------------------------*/
.about-area {
  text-align: center;
}
.single-about-box {
  padding: 25px;
  transition: all 0.4s;
}
.single-about-box i::before {
  color: #006600;
  font-size: 40px;
  transition: all 0.4s;
  display: block;
}
.single-about-box.active,
.single-about-box:hover {
  box-shadow: 0 0 5px #ccc;
}
.single-about-box.active i::before,
.single-about-box:hover i::before {
  color: #cccccc;
}
.single-about-box h4 {
  margin: 25px 0 15px;
}
/*--------------------------------------
---------- ABOUT AREA CSS END ----------
---------------------------------------*/

/*--------------------------------------
-------- FEATURE AREA CSS START --------
---------------------------------------*/
.feature-area {
  z-index: 1;
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.feature-area::before {
  content: "";
  top: 0;
  left: 0;
  opacity: 0.85;
  right: 0;
  bottom: 0;
  background: #001a00; /* Old browsers */
  background: -moz-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  );
  z-index: -1;
  position: absolute;
}
.jump {
  position: relative;
  animation-name: gentle-jump;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}
@keyframes gentle-jump {
  0% {
    top: -10px;
  }
  50% {
    top: 10px;
  }
  100% {
    top: -10px;
  }
}
.single-feature-box h4 {
  font-size: 20px;
  margin-bottom: 5px;
}
.single-feature-box li {
  margin-bottom: 45px;
  display: flex;
}
.single-feature-box li:last-child {
  margin-bottom: 0;
}
.feature-box-info {
  flex: 5;
}
.feature-box-icon {
  flex: 1;
  font-size: 20px;
  transition: all 0.4s;
}
.single-feature-box li:hover .feature-box-icon i::before {
  border-radius: 0;
}
.single-feature-box h2,
.single-feature-box h3,
.single-feature-box h4,
.single-feature-box {
  color: #fff;
}
.feature-box-icon i::before {
  border: 2px dashed yellow;
  width: 40px;
  display: inline-block;
  height: 40px;
  text-align: center;
  line-height: 38px;
  border-radius: 100%;
  transition: all 0.4s;
}
/*--------------------------------------
--------- FEATURE AREA CSS END ---------
---------------------------------------*/

/*--------------------------------------
-------- SHOWCASE AREA CSS START -------
---------------------------------------*/
.showcase-area {
  padding-bottom: 50px;
}
.single-showcase-box {
  padding: 0 0 40px;
}
.single-showcase-box p {
  margin: 20px 0 25px;
}
.single-showcase-box h4 {
  font-size: 20px;
}
/*--------------------------------------
--------- SHOWCASE AREA CSS END --------
---------------------------------------*/

/*--------------------------------------
---------- VIDEO AREA CSS START --------
---------------------------------------*/

.video-area::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #001a00; /* Old browsers */
  background: -moz-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  );
  opacity: 0.85;
  z-index: -1;
}
.video-popup i {
  display: inline-block;
  width: 100px;
  height: 100px;
  line-height: 80px;
  font-size: 25px;
  color: #001a00;
  border: 10px solid rgba(255, 255, 255, 0.7);
  border-radius: 50%;
  transition: all 0.4s;
  position: relative;
  z-index: 1;
}
.video-popup i::before {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  border-radius: 50%;
}
.video-popup i:hover {
  color: #fff;
}
.video-popup h1 {
  margin-top: 30px;
  line-height: 35px;
}
.video-popup i:hover::before {
  background: transparent;
}
/*--------------------------------------
----------- VIDEO AREA CSS END ---------
---------------------------------------*/

/*--------------------------------------
------ SCREENSHOT AREA CSS START -------
---------------------------------------*/
.screenshots-area .sec-title {
  margin-bottom: 15px;
}
.screenshot-wrap .slick-list {
  padding-bottom: 90px !important;
  padding-top: 80px !important;
}
.single-screenshot.slick-slide {
  z-index: 1;
}
.single-screenshot.slick-active {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
  z-index: 2;
  transition: all 0.3s ease-in 0s;
}
.single-screenshot.slick-center {
  position: relative;
  -webkit-transform: scale(1.25);
  -ms-transform: scale(1.25);
  transform: scale(1.25);
  z-index: 3;
  transition: all 0.3s ease-in 0s;
}
.single-screenshot img {
  width: 100%;
}
.slick-dots {
  text-align: center;
  margin-top: 10px;
}
.slick-dots li.slick-active button {
  background-color: #0074da;
}
.slick-dots li {
  display: inline-block;
}
.slick-dots li button {
  background: transparent;
  border: 2px solid #0074da;
  border-radius: 100%;
  font-size: 0;
  margin: 0 3px;
  padding: 0;
  cursor: pointer;
  width: 15px;
  height: 15px;
  display: inline-block;
}
.single-screenshot.slick-center img {
  padding: 35px 20px 55px;
}
/*--------------------------------------
-------- SCREENSHOT AREA CSS END -------
---------------------------------------*/

/*--------------------------------------
-------- PRICING AREA CSS START --------
---------------------------------------*/

.pricing-area::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #001a00; /* Old browsers */
  background: -moz-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  );
  opacity: 0.85;
  z-index: -1;
}
.pricing-area .sec-title,
.pricing-area .sec-title h2 {
  color: #fff;
}
.single-pricing-box {
  background: #fff;
  text-align: center;
  transition: all 0.4s;
}
.pricing-top {
  padding: 15px;
}
.pricing-top h4 {
  font-weight: 400;
}
.pricing-top p {
  font-style: italic;
  font-weight: 500;
}
.price {
  background-color: #666666;
  padding: 15px;
  color: #fff;
  transition: all 0.4s;
}
.price-details {
  padding: 25px 15px;
}
.price h1 {
  position: relative;
  padding-left: 13px;
  display: inline-block;
}
.price h1 span {
  font-size: 14px;
  position: absolute;
  top: 0;
  left: 0;
  line-height: 40px;
}
.price-details ul li {
  max-width: 204px;
  font-weight: 500;
  padding-bottom: 8px;
  margin: 0 auto 15px;
  border-bottom: 1px solid #666;
}
.price-details ul li:last-child {
  border-bottom: none;
  padding-bottom: 0;
}
.price-details .appao-btn {
  padding: 5px 35px;
  margin-top: 10px;
  border: 2px solid #666;
  color: #666;
  border-radius: 30px;
  transition: all 0.4s;
}
.single-pricing-box:hover .price {
  background-color: #001a00; /* Old browsers */
  background-image: -moz-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* FF3.6-15 */
  background-image: -webkit-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background-image: linear-gradient(
    to right,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  );
}
.single-pricing-box:hover .appao-btn {
  background-color: #001a00;
  background-image: -moz-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  );
  background-image: -webkit-linear-gradient(
    left,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  );
  background-image: linear-gradient(
    to right,
    #001a00 1%,
    #006600 24%,
    #009900 67%,
    #00b300 100%
  );
  color: #fff;
  border: 2px solid #009900;
}
/*--------------------------------------
--------- PRICING AREA CSS END ---------
---------------------------------------*/

/*--------------------------------------
------ TESTIMONIAL AREA CSS START ------
---------------------------------------*/
.author-img {
  width: 95px;
  height: 95px;
  border-radius: 100%;
  margin: 5px auto;
}
.author-img img {
  border-radius: 100%;
  border: 2px solid #0074da;
  box-shadow: 0 0 6px #0074da;
}
.single-testimonial-box {
  text-align: center;
}
.single-testimonial-box p {
  margin: 0 0 15px;
}
.single-testimonial-box h5 {
  margin: 35px 0 15px;
}
.testimonial-thumb > div {
  position: absolute;
  top: 5px;
  transform: scale(0.7);
  left: 50%;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  margin-left: -150px;
  z-index: 1;
}
.testimonial-thumb > div.thumb-next {
  left: auto;
  right: 50%;
  margin: 0 -150px 0 0;
}
/*--------------------------------------
------- TESTIMONIAL AREA CSS END -------
---------------------------------------*/

/*--------------------------------------
-------- COUNTERUP AREA CSS START --------
---------------------------------------*/

.counter-area::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #001a00; /* Old browsers */
  background: -moz-linear-gradient(
